/* Max Width Percentages */

.mw-15  { max-width: 15%; }
.mw-50  { max-width: 50%; }
.mw-70  { max-width: 70%; }
.mw-100  { max-width: 100%; }

/* Max Width Scale */

.mw1 { max-width: 16px; }
.mw2 { max-width: 32px; }
.mw3 { max-width: 64px; }
.mw4 { max-width: 128px; }
.mw5 { max-width: 256px; }
.mw6 { max-width: 512px; }
.mw7 { max-width: 768px; }
.mw8 { max-width: 1024px; }
.mw9 { max-width: 1536px; }

/* Max Width String Properties */

.mw-none { max-width: none; }

@media (--breakpoint-not-small) {
  .mw-100-ns  { max-width: 100%; }

  .mw1-ns { max-width: 16px; }
  .mw2-ns { max-width: 32px; }
  .mw3-ns { max-width: 64px; }
  .mw4-ns { max-width: 128px; }
  .mw5-ns { max-width: 256px; }
  .mw6-ns { max-width: 512px; }
  .mw7-ns { max-width: 768px; }
  .mw8-ns { max-width: 1024px; }
  .mw9-ns { max-width: 1536px; }

  .mw-none-ns { max-width: none; }
}

@media (--breakpoint-medium) {
  .mw-100-m  { max-width: 100%; }

  .mw1-m { max-width: 16px; }
  .mw2-m { max-width: 32px; }
  .mw3-m { max-width: 64px; }
  .mw4-m { max-width: 128px; }
  .mw5-m { max-width: 256px; }
  .mw6-m { max-width: 512px; }
  .mw7-m { max-width: 768px; }
  .mw8-m { max-width: 1024px; }
  .mw9-m { max-width: 1536px; }

  .mw-none-m { max-width: none; }
}

@media (--breakpoint-large) {
  .mw-100-l  { max-width: 100%; }

  .mw1-l {  max-width: 16px; }
  .mw2-l {  max-width: 32px; }
  .mw3-l {  max-width: 64px; }
  .mw4-l {  max-width: 128px; }
  .mw5-l {  max-width: 256px; }
  .mw6-l {  max-width: 512px; }
  .mw7-l {  max-width: 768px; }
  .mw8-l {  max-width: 1024px; }
  .mw9-l {  max-width: 1536px; }

  .mw-none-l { max-width: none; }
}
