/*
   SHADOWS
*/

:root {
  /* This one does not have color. The consumer should provide */
  --shadow-border: inset 0px 0px 0px 1px;

  --shadow-1: 0px 1px 1px  0px rgba(0, 0, 0, 0.08);
  --shadow-2: 0px 1px 1px  0px rgba(0, 0, 0, 0.16);
  --shadow-3: 0px 1px 8px  0px rgba(0, 0, 0, 0.16);
  --shadow-4: 0px 4px 12px 0px rgba(0, 0, 0, 0.20);
}
