/*
   COLOR VARIABLES
   - Gray scale (gray-9 to white)
   - Colors (primary success warning danger)

   https://anduin.gitbook.io/ui-guide/style/color
*/

:root {
  --color-gray-9: #182A38;
  --color-gray-8: #20394D;
  --color-gray-7: #4F6F89;
  --color-gray-6: #809AAD;
  --color-gray-5: #9BB0C2;
  --color-gray-4: #BACDDB;
  --color-gray-3: #DFE9F0;
  --color-gray-2: #EBF2F7;
  --color-gray-1: #F5F9FC;

  --color-white: #FFFFFF;

  --color-primary-5: #137CBD;
  --color-primary-4: #2B95D6;
  --color-primary-3: #48AFF0;
  --color-primary-2: #ACDBF8;
  --color-primary-1: #D3EEFF;

  --color-success-5: #0F9960;
  --color-success-4: #15B371;
  --color-success-3: #3DCC91;
  --color-success-2: #76D5AD;
  --color-success-1: #C7F2DF;

  --color-warning-5: #D9822B;
  --color-warning-4: #F29D49;
  --color-warning-3: #FFB366;
  --color-warning-2: #EFBE8E;
  --color-warning-1: #FAECDE;

  --color-danger-5: #DB3737;
  --color-danger-4: #F55656;
  --color-danger-3: #FF7373;
  --color-danger-2: #F5AAAA;
  --color-danger-1: #FCF0F0;

  --color-purple-5: #5943A9;
  --color-purple-4: #7055D4;
  --color-purple-3: #C7C2EF;
  --color-purple-2: #D7D1EC;
  --color-purple-1: #EBEBFF;
}
